import React, { ReactElement } from "react"
import { Box } from "../box"
import styled from "styled-components"
import { Text } from "../text"
import { t } from "../../i18n/i18n"
import { theme } from "../../theme/theme"

interface Props {
  isMobile: boolean
}

const StyledText = styled(Text)`
  font-size: 14px;
  line-height: 25px;
  color: ${theme("colors.text.0")};
`

export const FooterPrivacyPolicy = ({ isMobile }: Props): ReactElement => {
  const textAlign = isMobile ? "center" : "right"

  return (
    <Box
      borderTop={isMobile && `${theme("borders.1")} ${theme("colors.greys.0")}`}
      py="30px"
      display="flex"
      flexDirection="column"
    >
      <StyledText
        textAlign={textAlign}
        order={isMobile ? 0 : 2}
        my={!isMobile ? "22px" : 0}
      >
        {t("copyRight.privacyPolicy")}
      </StyledText>

      <StyledText
        textAlign={textAlign}
        order={isMobile ? 0 : 3}
        my={isMobile ? "11px" : 0}
      >
        {t("copyRight.termsAndConditions")}
      </StyledText>

      <StyledText textAlign={textAlign} order={isMobile ? 0 : 1} my={0}>
        {t("copyRight.reserved")}
      </StyledText>
    </Box>
  )
}
