import React, { ReactElement } from "react"
import { Box, BoxProps } from "../../ui/box"
import { Text } from "../../ui/text"
import { Button } from "../../ui/buttons/button"
import { t } from "../../i18n/i18n"
import { theme } from "../../theme/theme"
import { useMediaQuery } from "react-responsive"
import { withRouter, RouteComponentProps } from "react-router-dom"
import {
  InsuranceCardLiabilityImage,
  InsuranceCardComprehensiveImage,
  InsuranceCardHealthImage,
} from "../insurance-card-images"

type Props = BoxProps &
  RouteComponentProps & {
    title: string
    subTitle: string
    image: string
    icon: ReactElement
    link: string
    index: number
  }

export const InsuranceCard = withRouter(
  ({
    title,
    subTitle,
    image,
    link,
    icon,
    history,
    index,
    ...rest
  }: Props): ReactElement => {
    const isMobile = useMediaQuery({
      maxWidth: theme("breakpoints.3"),
    })
    const isSecond = index === 1

    return (
      <Box
        width={isMobile ? "315px" : "300px"}
        mx={isSecond && !isMobile && "40px"}
        my={isSecond && isMobile && "30px"}
        height="400px"
        bg="blues.4"
        borderRadius="15px"
        display="flex"
        flexDirection="column"
        alignItems="center"
        justifyContent="space-between"
        pt="24px"
        pb="40px"
        {...rest}
      >
        <Box>
          {index === 0 && <InsuranceCardLiabilityImage />}
          {index === 1 && <InsuranceCardComprehensiveImage />}
          {index === 2 && <InsuranceCardHealthImage />}
        </Box>

        <Box display="flex" flexDirection="column" alignItems="center">
          <Text
            mb={0}
            mt="24px"
            fontWeight="bold"
            fontSize={5}
            lineHeight="32px"
            color="blues.1"
          >
            {title}
          </Text>

          <Text my={0} color="blues.2">
            {subTitle}
          </Text>

          <Button
            text={t("general.compare")}
            mt="30px"
            variant="compare"
            width="144px"
            onClick={(): void => history.push(link)}
          />
        </Box>
      </Box>
    )
  },
)
