import {
  color,
  layout,
  position,
  space,
  border,
  borders,
  textStyle,
  colorStyle,
  typography,
  shadow,
  compose,
  ColorProps,
  SpaceProps,
  LayoutProps,
  FlexboxProps,
  BorderProps,
  BordersProps,
  PositionProps,
  TextStyleProps,
  TypographyProps,
  ShadowProps,
  ColorStyleProps,
} from "styled-system"
import styled from "styled-components"
import { ifProp, prop } from "styled-tools"

import { theme } from "../../theme/theme"
import { RestProps } from "../../core"

export type InputBaseProps = RestProps &
  ColorProps &
  SpaceProps &
  LayoutProps &
  FlexboxProps &
  BorderProps &
  BordersProps &
  PositionProps &
  TextStyleProps &
  TypographyProps &
  ShadowProps &
  ColorStyleProps & {
    name?: string
    placeholder?: string
    placeholderColor?: string
    children?: never
    ref?: React.RefObject<HTMLInputElement>
  }

export const InputBase = styled.input<InputBaseProps>`
  outline: none;
  box-sizing: border-box;
  appearance: none;

  ::placeholder {
    color: ${ifProp(
      "placeholderColor",
      prop("placeholderColor"),
      theme("colors.text.1"),
    )};
  }

  ${compose(
    color,
    space,
    layout,
    border,
    shadow,
    borders,
    position,
    textStyle,
    colorStyle,
    typography,
  )}
`

InputBase.defaultProps = {
  type: "text",
  color: theme("colors.text.1"),
  border: `${theme("borders.1")} ${theme("colors.greys.0")}`,
  fontFamily: "Inter, sans-serif",
  width: "100%",
  height: "60px",
  p: "13px 24px",
  borderRadius: "6px",
  value: "",
  fontSize: "18px",
  lineHeight: "34px",
  autoComplete: "off",
}

InputBase.displayName = "InputBase"
