import React, { ReactElement } from "react"
import {
  GetItemPropsOptions,
  GetMenuPropsOptions,
  GetPropsCommonOptions,
} from "downshift"
import styled from "styled-components"

import { SelectMenuItem } from "./select-menu-item"
import { Box } from "../box"

import { theme } from "../../theme/theme"
import { SelectItem } from "../../core"

interface Props {
  isOpen: boolean
  items: SelectItem[]
  getItemProps: (options: GetItemPropsOptions<SelectItem>) => void
  getMenuProps: (
    options?: GetMenuPropsOptions,
    otherOptions?: GetPropsCommonOptions,
  ) => void
}

const Root = styled(Box)<any>`
  position: relative;
  z-index: 6;
`

export const SelectMenu = ({
  items,
  isOpen,
  getItemProps,
  getMenuProps,
}: Props): ReactElement<Props> | null =>
  isOpen ? (
    <Root
      borderRadius="6px"
      bg="white"
      maxHeight="300px"
      overflow="auto"
      border={theme("borders.1") + theme("colors.greys.0")}
      {...getMenuProps()}
    >
      {items.map(
        (item, index): ReactElement => (
          <SelectMenuItem
            item={item}
            key={item.id}
            index={index}
            getItemProps={getItemProps}
          />
        ),
      )}
    </Root>
  ) : null
