import React, { ReactElement } from "react"
import { FieldProps } from "formik"
import { MaskedInputProps } from "react-text-mask"

import { Label } from "../label"
import { InputBase } from "../inputs/input-base"
import { Box } from "../box"
import { RestProps, Indexer, SVGIconProps } from "../../core"
import { TextareaBase } from "./textarea-base"

type Props = MaskedInputProps &
  RestProps & {
    label?: string
    valid?: boolean
    name: string
    children?: never
    fieldProps: FieldProps<Indexer>
    icon?: ReactElement<SVGIconProps>
    isTextArea?: boolean
  }

export const LabeledInput = ({
  label,
  valid,
  name,
  fieldProps,
  icon,
  isTextArea,
  ...props
}: Props): ReactElement<Props> => (
  <Box>
    <Label display="block" textStyle="normal">
      {label}
    </Label>

    <Box position="relative">
      {isTextArea ? (
        <TextareaBase {...fieldProps.field} {...props} />
      ) : (
        <InputBase {...fieldProps.field} {...props} />
      )}

      {icon}
    </Box>
  </Box>
)

LabeledInput.defaultProps = {
  label: "",
  valid: true,
}
