import React from "react"
import ReactDOM from "react-dom"
import { RawIntlProvider } from "react-intl"
import { ThemeProvider } from "styled-components"
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom"
import { intl } from "../src/i18n/i18n"

import { GlobalStyle, AppTheme } from "../src/theme/theme"
import { root, carLiability, comprehensiveCar, healthcare } from "./routes"
import { MainPage } from "./main/main.page"
import { CarLiabilityPage } from "./car-liability/car-liability.page"
import { ComprehensiveCarPage } from "./comprehensive-car/comprehensive-car.page"
import { HealthcarePage } from "./healthcare/healthcare.page"

ReactDOM.render(
  <ThemeProvider theme={AppTheme}>
    <RawIntlProvider value={intl}>
      <Router>
        <Switch>
          <Route path={root} exact component={MainPage} />
          <Route path={carLiability} component={CarLiabilityPage} />
          <Route path={comprehensiveCar} component={ComprehensiveCarPage} />
          <Route path={healthcare} component={HealthcarePage} />

          <Route path="*">
            <Redirect to={root} />
          </Route>
        </Switch>

        <GlobalStyle />
      </Router>
    </RawIntlProvider>
  </ThemeProvider>,
  document.getElementById("root"),
)
