import React, { ReactElement } from "react"
import { Box } from "../box"
import { SvgLocation } from "../icons/location"

import { Text } from "../text"
import { t } from "../../i18n/i18n"
import { SvgEmail } from "../icons/email"
import { theme } from "../../theme/theme"
import styled from "styled-components"

interface Props {
  isMobile: boolean
}

const StyledText = styled(Text)`
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 20px;
  font-size: ${theme("fontSizes.1")};
  color: ${theme("colors.text.0")};
`

export const FooterFeed = ({ isMobile }: Props): ReactElement => (
  <Box
    borderTop={isMobile && `${theme("borders.1")} ${theme("colors.greys.0")}`}
    pl={isMobile && "36px"}
    pr={isMobile && "92px"}
    py={isMobile && "30px"}
    width={!isMobile ? "217px" : "auto"}
    mt={!isMobile && "30px"}
  >
    <Box display="flex">
      <SvgLocation />

      <StyledText>{t("footer.location")}</StyledText>
    </Box>

    <Box display="flex" alignItems="center" mt={isMobile ? "15px" : "21px"}>
      <SvgEmail />

      <StyledText>{t("footer.email")}</StyledText>
    </Box>
  </Box>
)
