import React, { ReactElement } from "react"
import AliceCarousel from "react-alice-carousel"
import { Box } from "./box"
import styled from "styled-components"
import { useMediaQuery } from "react-responsive"
import { theme } from "../theme/theme"
import { PartnersItem } from "./partners-item"
import "react-alice-carousel/lib/alice-carousel.css"

const partnersImages: string[] = [
  "/images/partner1.png",
  "/images/partner2.png",
  "/images/partner3.png",
  "/images/partner1.png",
  "/images/partner2.png",
  "/images/partner3.png",
  "/images/partner1.png",
  "/images/partner2.png",
  "/images/partner3.png",
  "/images/partner1.png",
  "/images/partner2.png",
  "/images/partner3.png",
]

const partnersImagesMobile: string[] = [
  "/images/partner1mobile.png",
  "/images/partner2mobile.png",
  "/images/partner3mobile.png",
  "/images/partner1mobile.png",
  "/images/partner2mobile.png",
  "/images/partner3mobile.png",
  "/images/partner1mobile.png",
  "/images/partner2mobile.png",
  "/images/partner3mobile.png",
]

const Wrapper = styled(Box)`
  .alice-carousel__dots {
    margin-top: 40px;
  }

  .alice-carousel__dots-item {
    width: 8px;
    height: 8px;
    border: ${`${theme("borders.1")} ${theme("colors.greys.0")}`};
    background-color: ${theme("colors.white")};
  }

  .alice-carousel__dots-item.__active {
    width: 8px;
    height: 8px;
    background-color: ${theme("colors.primary")};

    @media screen and (max-width: ${theme("breakpoints.2")}) {
      background-color: ${theme("colors.greens.0")};
    }
  }
`

export const Partners = (): ReactElement => {
  const isMobile = useMediaQuery({
    maxWidth: theme("breakpoints.2"),
  })

  return (
    <Wrapper px={["30px", "30px", "205px"]} mt={isMobile ? "44px" : "51px"}>
      <AliceCarousel
        stagePadding={{
          paddingLeft: 0,
          paddingRight: 0,
        }}
        autoPlay
        infinite
        buttonsDisabled
        autoPlayInterval={4000}
        responsive={{
          0: {
            items: 3,
          },
          580: {
            items: 6,
          },
          1120: {
            items: 4,
          },
          1200: {
            items: 5,
          },
          1380: {
            items: 6,
          },
        }}
      >
        {isMobile
          ? partnersImagesMobile.map(
              (partnerImage: string): ReactElement => (
                <PartnersItem image={partnerImage} key={partnerImage} />
              ),
            )
          : partnersImages.map(
              (partnerImage: string): ReactElement => (
                <PartnersItem image={partnerImage} key={partnerImage} />
              ),
            )}
      </AliceCarousel>
    </Wrapper>
  )
}
