import React, { ReactElement, ChangeEvent } from "react"
import { GetInputPropsOptions } from "downshift"

import { Box, BoxProps } from "../box"
import { InputField } from "../inputs/input-field"

import { SelectItem } from "../../core"
import { SvgArrowDown } from "../icons/arrow-down"
import { theme } from "../../theme/theme"

export type SelectBaseComponentProps = GetInputPropsOptions & {
  onClick: () => void
  value: string | null
  autoComplete: string
  isDisabled?: boolean
}

interface Props {
  getInputProps: <T>(options?: T) => T & GetInputPropsOptions
  setState: (
    stateToSet: {
      highlightedIndex?: number | null
      inputValue?: string | null
      isOpen?: boolean
      selectedItem?: SelectItem | null
    },
    cb?: () => void,
  ) => void
  inputProps?: BoxProps
  clearSelection: (cb?: any) => void
}

export const SelectBase = ({
  getInputProps,
  setState,
  inputProps,
  clearSelection,
}: Props): ReactElement => {
  const handleClick = (): void => {
    setState({
      isOpen: true,
    })
  }

  return (
    <Box
      width="100%"
      height="100%"
      position="relative"
      display="flex"
      alignItems="center"
    >
      <Box width="100%">
        <InputField
          onClick={handleClick}
          name="select"
          fontSize={theme("fontSizes.4")}
          icon={
            <SvgArrowDown
              box={{
                position: "absolute",
                top: "22px",
                right: "26px",
                cursor: "pointer",
                onClick: handleClick,
              }}
            />
          }
          {...inputProps}
          {...getInputProps({
            onChange: (event: ChangeEvent<HTMLInputElement>): void => {
              !event.target.value && clearSelection()
            },
          })}
          autoComplete="off"
        />
      </Box>
    </Box>
  )
}
