import React, { ReactElement } from "react"
import { useMediaQuery } from "react-responsive"

import { Text } from "../../ui/text"
import { Box } from "../../ui/box"
import { theme } from "../../theme/theme"
import { t } from "../../i18n/i18n"
import { Button } from "../../ui/buttons/button"
import { HeaderImage } from "./header-image"

interface Props {
  isMobile: boolean
}

export const HeaderLeft = ({ isMobile }: Props): ReactElement => {
  const isBigImg = useMediaQuery({
    maxWidth: theme("breakpoints.0"),
  })

  return (
    <Box width="100%" display="flex" flexDirection="column">
      <Box
        ml={isMobile ? 0 : "186px"}
        display={isMobile ? "flex" : "block"}
        flexDirection="column"
        alignItems="center"
      >
        <Text
          maxWidth={isMobile ? "100%" : "356px"}
          textAlign={isMobile ? "center" : "left"}
          color="primary"
          fontWeight="900"
          fontSize={isBigImg ? 6 : 8}
          lineHeight={isBigImg ? "32px" : "45px"}
          my={0}
          px={isMobile ? "45px" : 0}
        >
          {t("comprehensiveCar.title")}
        </Text>

        <Text
          textAlign={isMobile ? "center" : "left"}
          maxWidth={isMobile ? "100%" : "230px"}
          fontSize={4}
          lineHeight="34px"
          mt={isMobile ? "10px" : "14px"}
          mb={0}
          px={isMobile ? "70px" : 0}
        >
          {t("header.text")}
        </Text>

        {isMobile && (
          <Button text={t("general.compare")} width="315px" mt="30px" />
        )}
      </Box>

      <HeaderImage isMobile={isMobile} isBigImg={isBigImg} />
    </Box>
  )
}
