import React, { ReactElement } from "react"
import Downshift from "downshift"

import { Box, BoxProps } from "../box"
import { SelectMenu } from "./select-menu"
import { SelectBase } from "./select-base"
import { InputFeedback } from "../inputs/input-feedback"

import { SelectItem } from "../../core"

type Props = BoxProps & {
  id: string
  items: SelectItem[]
  onChange?: (item: SelectItem) => {}
  selectedValue?: SelectItem
  inputProps?: BoxProps
  hasArrowIcon?: boolean
}

export const Select = ({
  id,
  items,
  onChange,
  selectedValue,
  inputProps,
  ...rest
}: Props): ReactElement<Props> => (
  <Downshift
    id={id}
    selectedItem={selectedValue}
    itemToString={(item: SelectItem | null): string => (item ? item.name : "")}
  >
    {({
      isOpen,
      getRootProps,
      getItemProps,
      getInputProps,
      getMenuProps,
      setState,
      clearSelection,
    }): ReactElement => (
      <Box {...rest} {...getRootProps()}>
        <SelectBase
          getInputProps={getInputProps}
          setState={setState}
          inputProps={inputProps}
          clearSelection={clearSelection}
        />

        <SelectMenu
          items={items}
          isOpen={isOpen}
          getMenuProps={getMenuProps}
          getItemProps={getItemProps}
        />

        {!isOpen && <InputFeedback name={id} />}
      </Box>
    )}
  </Downshift>
)

Select.defaultProps = {
  width: "100%",
  height: "60px",
  placeholder: "",
  border: 0,
  zIndex: 1,
  openOnInputClick: true,
  hasArrowIcon: true,
}
